import * as Yup from 'yup'

const email = Yup.string().email('Correo Electrnico invlido').required('Coloca tu Correo electrnico')

const registerPassword = Yup.string()
.required('Contrasea Requerida')
.min(6, 'Contrasea demasiado corta.')
.max(18, 'Contrasea demasiado larga')
.matches(/^\S*$/,('La contrasea no puede contener espacios'))
.matches(/[a-z]/, 'Debe contener al menos una letra minscula')
.matches(/[A-Z]/, 'Debe contener al menos una letra mayscula')

const password = Yup.string().required('Contrasea Requerida')

const confirmPassword = Yup.string().oneOf([Yup.ref('registerPassword'), null], 'Las contraseas deben coincidir').required('Las contraseas deben coincidir')

export const registerSchema = Yup.object({
    email,
    registerPassword,
    confirmPassword,
    username: Yup.string().required('Coloca tu nombre').min(3, 'El nombre es demasiado corto').max(20, 'El nombre es demasiado largo')
})

export const loginSchema = Yup.object({
    email,
    password
})

export const emailCheckSchema = Yup.object({
    email
})

export const resetPassword = Yup.object({
    registerPassword,
    confirmPassword
})
